<template>
    <div style="padding-left: 10%;">
        <div>
            <div>USDT本位永续合约</div>
            <el-menu :default-active="$route.path" :default-openeds="level1" router>
                <template v-for="route in $router.options.routes" v-if="!route.meta.hidden">
                    <el-menu-item v-if="!route.children || route.children.length == 1" :index="route.path"
                        :key="route.path">
                        {{ route.meta.name }}
                    </el-menu-item>
                    <el-submenu v-else :index="route.path">
                        <template slot="title">{{ route.meta.name }}</template>
                        <el-menu-item v-for="child in route.children" v-if="!child.meta.hidden"
                            :index="route.path + '/' + child.path" :key="route.path + '/' + child.path">{{ child.meta.name
                            }}</el-menu-item>
                    </el-submenu>
                </template>
            </el-menu>
        </div>
        <div>
            <div>Socket文档</div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
        };
    },
    computed: {
        level1() {
            return this.$router.options.routes.map(function (route) {
                return route.path;
            });
        }
    }
};
</script>
<style>
.el-submenu .el-menu {
    background-color: white;
}
</style>


