const app = {
  state: {
    sidebar: '1111',
  },
  mutations: {
    SET_SIZE: (state, size) => {
      state.sidebar = 'change'
    }
  },
  actions: {
    setSize({ commit }, size) {
      commit('SET_SIZE', size)
    }
  }
}

export default app



