import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Home = () => import('@/views/main.vue')
const Notfound = () => import('@/views/notfound.vue')
const routes = [{
  path: '/swap',
  component: Home,
  meta: {
      name: '介绍'
  },
  children: [{
      path: '', 
      component: Home
  }]
}, {
  path: '/swap/authentication',
  component: Home,
  meta: {
      name: '签名认证'
  },
  children: [{
      path: '',
      redirect: 'create-api',
      meta: {
          hidden: true 
      }
  }, {
      path: 'create-api',
      component: Home,
      meta: {
          name: '创建API'
      }
  }, {
      path: 'request-body',
      component: Home,
      meta: {
          name: '请求内容'
      }
  }, {
      path: 'sign',
      component: Home,
      meta: {
          name: '签名说明'
      }
  }, {
      path: 'request-response',
      component: Home,
      meta: {
          name: '请求交互'
      }
  }]
}, {
  path: '/swap/base-info',
  component: Home,
  meta: {
      name: '基本信息'
  },
  children: [{
      path: '',
      redirect: 'recharge',
      meta: {
          hidden: true
      }
  }, {
      path: 'error-code',
      component: Home,
      meta: {
          name: '常见错误码'
      }
  }, {
      path: 'timestemp',
      component: Home,
      meta: {
          name: '时间戳规范'
      }
  },{
      path: 'digit-rule',
      component: Home,
      meta: {
          name: '数字规范'
      }
  },{
      path: 'rate-limit',
      component: Home,
      meta: {
          name: '频率限制'
      }
  },{
      path: 'query-system-time',
      component: Home,
      meta: {
          name: '查询系统时间'
      }
  }]
}];
routes.push({
  path: '*', 
  component: Notfound,
  meta: {
      hidden: true
  }
});
export default new Router({
	// mode: 'history',
  routes: routes
})



