<template>
  <div class="app">
      <Navbar class="app__navbar"></Navbar>
      <el-row class="app__main-box">
          <el-col :span="3" class="app__router-nav-box">
              <RouterNav></RouterNav>
          </el-col>
          <el-col :span="21" class="app__main-content-box">
              <router-view></router-view>
          </el-col>
      </el-row>
  </div>
</template>
<script>
  import Navbar from './views/nav.vue';
  import RouterNav from './views/menu.vue';

  export default {
      data() {
          return {
          };
      },
      components: {
          Navbar,
          RouterNav
      }
  };
</script>
<style>
  .app,
  .app__main-box,
  .app__router-nav-box,
  .app__main-content-box {
      height: 100%;
  }
  .app__navbar {
      position: fixed;
      top: 0;
      z-index: 1;
      width: 100%;
  }
  .app__main-box {
      padding-top: 60px;
  }
  .app__router-nav-box {
      overflow: auto;
  }
  .app__main-content-box {
      overflow: auto;
  }
</style>


